import {BottomInfo, BottomText, GreenMarkerIcon, ShareIcon,} from "../styles/cardStyles";
import {FC} from "react";
import {SearchStore} from "../stores/SearchStore";
import {MapglStore} from "stores/MapglStore";
import {PostMessageData} from "../api/interfaces/postMassage.interface";
import {PostMessageTypeEnum} from "../enums/PostMessageType.enum";

interface AdministrativeDivisionCardProps {
  isOpen: boolean;
  searchStore: SearchStore;
  shareData: (arg0: PostMessageData) => void;
  mapglStore: MapglStore;
  coordinates: [number, number];
}

export const AdministrativeDivisionCard: FC<AdministrativeDivisionCardProps> =
  ({ isOpen, searchStore, shareData, mapglStore,coordinates }) => {
    return (
      <>
        {searchStore.currentObject?.address ? (
          <BottomInfo>
            <GreenMarkerIcon />
            <BottomText>
              {searchStore.currentObject?.address?.postcode &&
                searchStore.currentObject.address?.postcode + ", "}
              {searchStore.currentObject?.address_name}
            </BottomText>
            {!isOpen ? (
              <ShareIcon
                onClick={() =>
                  shareData(
                      {type: PostMessageTypeEnum.SHARE_INFO, data: searchStore.currentObject
                            ? `${searchStore.currentObject?.point.lon} ${searchStore.currentObject?.point.lat}`
                            : ""}
                  )
                }
              />
            ) : null}
          </BottomInfo>
        ) : (
          <BottomInfo>
            <GreenMarkerIcon onClick={() => mapglStore.setCenter(coordinates)} />
            <BottomText onClick={() => mapglStore.setCenter(coordinates)}>
              {searchStore.currentObject?.full_name}
            </BottomText>
            {!isOpen ? (
              <ShareIcon
                onClick={() =>
                  shareData(
                      {type: PostMessageTypeEnum.SHARE_INFO, data: searchStore.currentObject
                            ? `${searchStore.currentObject?.point.lon} ${searchStore.currentObject?.point.lat}`
                            : ""}
                  )
                }
              />
            ) : null}
          </BottomInfo>
        )}
      </>
    );
  };
